/*
*
*column set
*/
@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

.lbbItemContent {
	display: none;
}

.ghbModalMainSection {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #2a2a2d;

	.ghbChildSection {
		position: fixed;
		right: 0;
		left: 0;
		top: 45%;
		margin: 0 auto;
		transform: translateY(-50%) scale(.8);
		background: #fff;
		-webkit-transition: all .3s ease;
		-o-transition: all .3s ease;
		transition: all .3s ease;
		-webkit-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
		-moz-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
		box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.1);
		// opacity: 0;
		width: 65%;
		height: 60%;
		// visibility: hidden;
		overflow-x: auto;
		border-radius: 7px;
		padding: 70px 35px;
		z-index: 99999;

		.closeBtn {
			position: absolute;
			top: 14px;
			right: 0;
			width: 50px;
			height: 50px;
			cursor: pointer;
		}
	}


}

// Light Box Block
.wp-block-lbb-lightbox {

	* {
		box-sizing: border-box;
	}

	button {
		cursor: pointer;
	}

	a {
		text-decoration: none !important;
		border: none;
		outline: 0;
		box-shadow: none;
		transition: 0.3s;

		&:hover {
			transition: 0.3s;
		}
	}

	button {
		border: none;
		padding: 0;
		margin: 0;
	}

	.lbbLightBox {
		display: grid;
		justify-content: start;

		&.llbButton {
			display: block;

			.flex {
				display: flex;
			}
		}

		.iframeHidden {
			display: none;

		}

		.lbbContent_area {
			position: relative;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.music_icon {
				position: absolute;
				left: 6px;
				top: 4px;
				width: 30px;
				height: 30px;
				background: #fff;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;

				i {
					color: #4b4f58;
					font-size: 17px;
				}

				&.video_icon {
					padding-left: 9px;
				}

				&.audio_icon {
					padding-left: 4px;
				}

				&.content-icon {
					padding-left: 5px;
				}
			}
		}

		img {
			border-radius: 5px;
		}

		.content-thumbnail {
			cursor: pointer;
			position: relative;
			width: 100%;
			height: 100%;

			img {
				border-radius: 5px;
			}

			.contnet-svgImage {
				position: absolute;
				left: 6px;
				top: 4px;
				width: 30px;
				height: 30px;
				background: #fff;
				border-radius: 50%;
				display: flex;
				align-items: center;
				padding-left: 5px;

				i {
					font-size: 17px;
					color: #4b4f58;
				}
			}
		}

		button {
			display: flex;
			align-items: center;
			justify-content: center;
			line-height: 100%;
			font-weight: 500;
			max-width: 100%;
			max-height: 100%;
			transition: 0.3s;

			&:hover {
				transition: 0.3s;
			}

		}
	}

	.masonry {
		position: relative;

		.lbbContent_area {
			position: absolute;
		}
	}
}

.lbbContentBox.fancybox__content {
	width: 90% !important;
}

.lbbContentBox * {
	line-height: 1.4em;

	.fancybox__content {}
}



.iframeHidden {
	display: none;
	width: 1200px;

	.feedPopUpArea {
		width: 100%;

		.feedPopUp {
			width: 100%;

			.iframe {
				width: 100%;
				height: 633px;
				display: block;
				visibility: unset;
			}
		}
	}
}

